<template>
  <div class="page">
    <div
      class="overlay-image"
      :style="{
        background:
          'url(\'' + adminData.image1 ||
          ''.replace('http', 'https') + '\') fixed center center no-repeat',
        backgroundSize: 'cover',
      }"
    ></div>
    <div
      v-if="stage && userId"
      style="background: rgb(18 24 27 / 70%)"
      class="content-fixed"
    >
      <div class="container">
        <div class="text-center titlee">{{ "الكورسات" }}</div>
        <div v-if="loading" class="center">
          <loading color="#fff" />
        </div>
        <br />
        <p class="text-center not-videos" v-if="sections.length < 1 && !loading">
          لا يوجد كورسات بعد
        </p>
        <div v-if="!loading && sections.length > 0" class="cards w-100 row" dir="rtl">
          <div v-for="section in sections" :key="section._id" class="col-md-4">
            <div
              :class="{ not_allowed: !section.allowed && section.price > 0 }"
              @click="() => gotoExams(section.number)"
              class="card"
              :key="section._id"
              :style="{
                background:
                  'url(' + section.image.replace('http', 'https') + ') center center',
                backgroundSize: '100% 100%',
              }"
            >
              <div class="section">
                <h3>{{ section.name }}</h3>
                <h2 dir="ltr">{{ section.price | number }} جنيه</h2>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!---------------------------------------------------------------------->
    <!---------------------------------------------------------------------->
    <!---------------------------------------------------------------------->
    <!---------------------------------------------------------------------->
    <!---------------------------------------------------------------------->
    <div
      v-if="!stage || !userId"
      style="background: rgb(18 24 27 / 70%)"
      class="content-fixed"
    >
      <div class="container">
        <div class="text-center titlee">{{ "جميع الكورسات" }}</div>
        <div v-if="loading" class="center">
          <loading color="#fff" />
        </div>
        <br />
        <p
          class="text-center not-videos"
          v-if="Object.keys(sections).length < 1 && !loading"
        >
          لا يوجد كورسات بعد
        </p>
        <div v-if="!loading" dir="rtl">
          <div v-for="(sections, stage) in sections" :key="stage" class="cards">
            <h3 class="page-title py-4" style="color: #eee">{{ stage | stage }}</h3>
            <hr />
            <div class="w-100 row">
              <div v-for="section in sections" :key="section.number" class="col-md-4">
                <div
                  @click="() => gotoExams(section.number, stage)"
                  class="card"
                  :key="section._id"
                  :style="{
                    background:
                      'url(' + section.image.replace('http', 'https') + ') center center',
                    backgroundSize: '100% 100%',
                  }"
                >
                  <div class="section">
                    <h3>{{ section.name }}</h3>
                    <h2 dir="ltr">{{ section.price | number }} جنيه</h2>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Loading from "../components/loading.vue";
export default {
  components: {
    Loading,
  },
  created() {
    this.loading = true;
    axios.get("/getSectionsForStudent/" + this.stage + "/" + this.userId).then((res) => {
      this.loading = false;
      this.sections = res.data.sections;
    });
  },
  data() {
    return {
      sections: [],
      loading: false,
    };
  },
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
    stage() {
      return this.$store.getters.stage;
    },
    userId() {
      return this.$store.getters.userId;
    },
  },
  methods: {
    gotoExams(unit, stage) {
      if (stage) this.$router.push("/videos/" + stage + "/" + unit);
      else if (!stage) this.$router.push("/videos/" + unit);
    },
  },
};
</script>

